* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: black; }

body {
  display: flex;
  align-items: center;
  justify-content: center; }

#root {
  justify-self: start; }

.text {
  font-size: 100px;
  margin: 100px 50px;
  color: white; }

#root {
  width: 100%; }

.input-range, .text {
  width: 90% !important; }

.input-range {
  margin-left: 60px;
  position: fixed !important;
  bottom: 40px; }

.pause-button {
  color: blue;
  margin-left: 60px;
  position: fixed !important;
  bottom: 40px;
  right: 50px; }

.pause-button:hover {
  cursor: pointer; }

.display-linebreak {
  white-space: pre-line; }

.dropzone {
  border: red dashed 2px;
  height: 100px;
  color: white;
  font-size: 40px;
  margin: 40px;
  padding: 40px; }
